.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 33px;
  padding-bottom: 33px;
}

.footer__hashtag {
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .footer__wrapper {
    flex-direction: row;
  }
}
